<template>
  <b-tab :title="item_slot">
    <b-card-text>
      <b-row>
        <b-col md="9">
          <b-form-input
            v-model="input_filter"
            placeholder="Filter..."
            class="mb-3"
          ></b-form-input>
          
          <Items
            :items="filter(input_filter, items)" 
            :item_slot='item_slot'
            :input="input"
          ></Items>
        </b-col>

        <b-col md="3">
          <b-form-radio-group
            class="d-block"
            v-model="input.paint"
            :options="paints"
            buttons
            stacked
            :disabled="paintDisabled()"
            button-variant="outline-secondary"
          ></b-form-radio-group>
        </b-col>
      </b-row>
    </b-card-text>
  </b-tab>
</template>

<script>
import Items from './Items.vue'

var paints = [
  {text: 'Unpainted', value: ''},
  'Black',
  'Cobalt',
  'Crimson',
  'Forest Green',
  'Grey',
  'Lime',
  'Orange',
  'Pink',
  'Purple',
  'Saffron',
  'Burnt Sienna',
  'Sky Blue',
  'Titanium White'
];

export default {  
  name: 'ItemTab',
  data() {
    return {
      input_filter: '',
      paints: paints
    }
  },
  props: {
    items: Array,
    paintable: Object,
    item_slot: String,
    input: Object
  },
  components: {
    Items
  },
  methods: {
    paintDisabled: function() {
      if (!this.paintable[this.input.item]) {
        this.input.paint = '';
        return true;
      }
      return false;
    },
    filter: function(needle, haystack) {
			return haystack.filter(i => i.toUpperCase().includes(needle.toUpperCase()))
    }
  }
}
</script>