<template>
  <b-container id="app">
    <b-form>
      <h1 class="my-4">RLCD Submission Form</h1>
      <b-row>
        <b-col sm="12" md="9">
          <b-card no-body>
            <b-card-body>
              <b-row>
                <b-col>
                  <b-input-group prepend="/u/">
                    <b-form-input v-model="input.username" placeholder="Reddit username"></b-form-input>
                  </b-input-group>
                </b-col>
                <b-col>
                  <b-form-radio-group
                    id="team-select"
                    v-model="input.team"
                    :options="[{text: 'Blue Team', value: 'Blue'}, {text: 'Orange Team', value: 'Orange'}]"
                    buttons
                    button-variant="outline-secondary"
                    class="float-right"
                  ></b-form-radio-group>
                </b-col>
              </b-row>
            </b-card-body>
            <b-tabs id="items" card>
              <ItemTab
                :items="items['Body']"
                :paintable="items.paintable['Body']"
                :input="input['Body']"
                item_slot='Body'
              ></ItemTab>
              <ItemTab
                :items="decalsForBody()"
                :paintable="items.paintable['Decal']"
                :input="input['Decal']"
                item_slot='Decal'
              ></ItemTab>
              <PaintFinishTab
                :team="input.team"
                :input="input['Paint Finish']"
                :items="items['Paint Finish']"
              ></PaintFinishTab>
              <ItemTab
                :items="items['Wheels']"
                :paintable="items.paintable['Wheels']"
                :input="input['Wheels']"
                item_slot='Wheels'
              ></ItemTab>
              <ItemTab
                :items="items['Rocket Boost']"
                :paintable="items.paintable['Rocket Boost']"
                :input="input['Rocket Boost']"
                item_slot='Rocket Boost'
              ></ItemTab>
              <ItemTab
                :items="items['Topper']"
                :paintable="items.paintable['Topper']"
                :input="input['Topper']"
                item_slot='Topper'
              ></ItemTab>
            </b-tabs>
            <b-card-body>
              <b-card-title>Comment</b-card-title>
              <b-form-textarea
                id="textarea"
                v-model="input.comment"
                placeholder="Enter something..."
                rows="3"
                max-rows="6"
              ></b-form-textarea>
            </b-card-body>
          </b-card>
        </b-col>
        <b-col cols="3">
          <Summary :input="input"></Summary>
          <b-button block variant="primary" class="mt-4">Submit</b-button>
        </b-col>
      </b-row>
    </b-form>
  </b-container>
</template>

<script>
import Summary from './components/Summary.vue'
import ItemTab from './components/ItemTab.vue'
import PaintFinishTab from './components/PaintFinishTab.vue'

var items = require('./items.csv')

var input = {
  'Body': {item: 'Octane', paint: '', filter: ''},
  'Decal': {item: 'None', paint: '', filter: ''},
  'Paint Finish': {
    primary: {color: 'd6', item: 'Glossy', filter: ''},
    accent: {color: 'a1', item: 'Glossy', filter: ''}
  },
  'Wheels': {item: 'OEM', paint: '', filter: ''},
  'Rocket Boost': {item: 'Standard', paint: '', filter: ''},
  'Topper': {item: 'None', paint: '', filter: ''},
  team: 'Blue',
  username: '',
  comment: ''
};

export default {
  data() {
    return {
      input: input,
      items: items
    }
  },
  components: {
    Summary,
    ItemTab,
    PaintFinishTab
  },
  methods: {
    decalsForBody: function() {
      var input_decal = input['Decal'].item;
      var input_body = input['Body'].item;
      var all_decals = items['Decal'];
      var decal = '';
      var body_decals = []; // all the possible decals for the selected body

      for (var i in all_decals) {
        decal = all_decals[i];
        if (decal.includes(':')) {
          // match exactly the first N chars
          if (decal.substring(0, input_body.length + 2) == input_body + ': ') {
            body_decals.push(decal);
          }
        } else { // decals with no ':', so BMDs and univesrals
          body_decals.push(decal);
        }
      }

      // a selected decal doesn't match the selected body, reset
      if (!body_decals.includes(input_decal)) {
        input['Decal'].item = 'None';
      }

      return body_decals;
    }
  }
}
</script>
