<template>
  <b-row class="overflow-y">
    <b-col cols="3" v-for="i in items" :key="i">
      <div class="btn-group btn-group-toggle btn-block mb-4">
        <label
          class="btn btn-sm btn-outline-primary"
          :class="{active: input.item == i}"
        >
          <input
            type="radio"
            v-model="input.item"
            :value="i"
          >
          <b-img-lazy
            :src="findThumb(i)"
            fluid-grow
            rounded
          ></b-img-lazy>
          <span>{{ i }}</span>
        </label>
      </div>
    </b-col>
  </b-row>
</template>

<script>
var thumbs = require('../thumbs.json')
var config = require('../../vue.config.js') // I need a better way to do this

export default {
  name: 'Items',
  data() {
    return {
      thumbs: thumbs
    }
  },
  props: {
    items: Array,
    item_slot: String,
    input: Object // user input
  },
  methods: {
    findThumb: function(item_name) {
      var slot = this.item_slot;
      if (this.thumbs[slot][item_name]) {
        return config.publicPath + 'img/thumbs/' + this.thumbs[slot][item_name] + '.png'
      } else {
        return config.publicPath + 'img/nothumb.png'
      }
    }
  }
}
</script>

<style scoped>
#items .overflow-y {
  overflow-y: auto;
  height: 30em;
}

#items .overflow-y .btn {
  padding: 0;
  border-width: 0;
  border-width: 5px;
  border-color: transparent;
  text-shadow: 0px 0px 4px black;
}

#items .overflow-y .btn:hover {
  border-width: 5px;
}

#items .overflow-y .btn span {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  color: white;
  margin: 2px;
}

#items .overflow-y .btn img {
  background-color: black;
}

#items .btn-group .active {
  border-width: 5px;
}
</style>