<template>
  <b-card no-body>
    <b-card-header class="text-center">Summary</b-card-header>
    <b-list-group flush>
      <b-list-group-item>
        <h6>Reddit Username</h6>
        /u/{{ input.username }}
      </b-list-group-item>
      <b-list-group-item>
        <h6>Team</h6>
        {{ input.team }}
      </b-list-group-item>
      <b-list-group-item>
        <h6>Body</h6>
        {{ input['Body'].paint + ' ' + input['Body'].item }}
      </b-list-group-item>
      <b-list-group-item>
        <h6>Decal</h6>
        {{ input['Decal'].paint + ' ' + input['Decal'].item }}
      </b-list-group-item>
      <b-list-group-item>
        <h6>Primary Paint Finish</h6>
        <div class="team-colors">
          <div :class="input.team">
            <div class="color-preview" :class="input['Paint Finish'].primary.color"></div>
          </div>
        </div>
        {{ input['Paint Finish'].primary.item }}
      </b-list-group-item>
      <b-list-group-item>
        <h6>Accent Paint Finish</h6>
        <div class="team-colors">
          <div class="Accent">
            <div class="color-preview" :class="input['Paint Finish'].accent.color"></div>
          </div>
        </div>
        {{ input['Paint Finish'].accent.item }}
      </b-list-group-item>
      <b-list-group-item>
        <h6>Wheels</h6>
        {{ input['Wheels'].paint + ' ' + input['Wheels'].item }}
      </b-list-group-item>
      <b-list-group-item>
        <h6>Boost</h6>
        {{ input['Rocket Boost'].paint + ' ' + input['Rocket Boost'].item }}
      </b-list-group-item>
      <b-list-group-item>
        <h6>Topper</h6>
        {{ input['Topper'].paint + ' ' + input['Topper'].item }}
      </b-list-group-item>
      <b-list-group-item>
        <h6>Comment</h6>
        {{ input.comment }}
      </b-list-group-item>
    </b-list-group>
  </b-card>
</template>

<script>
export default {
  name: 'Summary',
  props: {
    input: Object
  }
}
</script>

<style scoped>
.color-preview {
  height: 2em;
}
</style>