const path = require('path');

module.exports = {
  chainWebpack: config => {
      config.module
      .rule('items-loader')
      .test(/items\.csv$/)
      .use('items-loader')
      .loader(path.resolve('./items-loader.js'))
  },
  publicPath: process.env.NODE_ENV === 'production'
    ? '/form/'
    : '/'
}